import React from "react";
import "./styles.css";

function Header({ pais, desde, hasta }) {
  const options = {
    weekday: "long",
    year: "numeric",
    month: "long",
    day: "numeric"
  };
  let desdeNatural = new Date(desde + " 00:00:00").toLocaleDateString(
    "es-AR",
    options
  );
  let hastaNatural = new Date(hasta + " 00:00:00").toLocaleDateString(
    "es-AR",
    options
  );
  return (
    <div className="header">
      <h1>HOTELES</h1>
      <h3>
        {pais !== "todos"
          ? "El país seleccionado es " + pais
          : "Cualquier país"}
      </h3>
      <h3>Desde {desde !== "" ? desdeNatural : "cualquier fecha"}</h3>
      <h3>Hasta {hasta !== "" ? hastaNatural : "cualquier fecha"}</h3>
    </div>
  );
}

export default Header;
