import React from "react";
import "./styles.css";

function Hotel(props) {
  const options = {
    weekday: "long",
    year: "numeric",
    month: "long",
    day: "numeric"
  };

  let dateEntrada = new Date(props.disponibleIn);
  let inNatural = dateEntrada.toLocaleDateString("es-AR", options);

  let dateSalida = new Date(props.disponibleOut);
  let outNatural = dateSalida.toLocaleDateString("es-AR", options);

  const precioCo = "$".repeat(props.precio);

  return (
    <div className="hotel">
      <img width="100%" src={props.imagen} alt={props.nombre} />
      <h3>{props.nombre}</h3>
      <p>{props.descripcion}</p>
      <p>{inNatural}</p>
      <p>{outNatural}</p>
      <h4>
        <img width="15px" src={props.iconoUb} alt="" /> {props.pais},{" "}
        {props.ciudad}
      </h4>
      <h5>
        <img width="12px" src={props.iconoHab} alt="" /> {props.habitaciones}{" "}
        habitaciones
      </h5>
      <h5>{precioCo}</h5>

      <button className="btn-reserva">Reservar</button>
    </div>
  );
}

export default Hotel;
