import React from "react";
import "./styles.css";
import Hotel from "./Hotel";

function Hoteles(props) {
  return (
    <div className="hoteles">
      {props.listaHoteles.map((hotel) => {
        return (
          <Hotel
            nombre={hotel.name}
            pais={hotel.country}
            imagen={hotel.photo}
            descripcion={hotel.description}
            ciudad={hotel.city}
            habitaciones={hotel.rooms}
            precio={hotel.price}
            iconoHab={hotel.iconroom}
            iconoUb={hotel.iconlocation}
            disponibleIn={hotel.availabilityFrom}
            disponibleOut={hotel.availabilityTo}
          />
        );
      })}
    </div>
  );
}

export default Hoteles;
