import React, { useState } from "react";
import "./styles.css";
import Header from "./Header";
import Filtros from "./Filtros";
import Hoteles from "./Hoteles";
import { hotelsData } from "./hotelsData.js";

export default function App() {
  const [pais, setPais] = useState("todos");
  const [tamaño, setTamaño] = useState("todos");
  const [precio, setPrecio] = useState("todos");
  const [desde, setDesde] = useState("");
  const [hasta, setHasta] = useState("");

  let hotelesFiltrados = hotelsData.filter((hotel) => {
    if (pais === "todos") {
      return true;
    } else if (hotel.country === pais) {
      return true;
    } else {
      return false;
    }
  });

  hotelesFiltrados = hotelesFiltrados.filter((hotel) => {
    if (precio === "todos") {
      return true;
    } else if (hotel.price === precio.length) {
      return true;
    } else {
      return false;
    }
  });

  hotelesFiltrados = hotelesFiltrados.filter((hotel) => {
    if (tamaño === "todos") {
      return true;
    } else if (tamaño === "pequeño") {
      return hotel.rooms <= 10;
    } else if (tamaño === "mediano") {
      return hotel.rooms > 10 && hotel.rooms < 20;
    } else {
      return hotel.rooms >= 20;
    }
  });

  hotelesFiltrados = hotelesFiltrados.filter((hotel) => {
    const fecha = new Date(desde + " 00:00:00").getTime();
    if (desde === "") {
      return true;
    } else {
      return fecha >= hotel.availabilityFrom && fecha <= hotel.availabilityTo;
    }
  });

  hotelesFiltrados = hotelesFiltrados.filter((hotel) => {
    const fecha = new Date(hasta + " 00:00:00").getTime();
    if (hasta === "") {
      return true;
    } else {
      return fecha >= hotel.availabilityFrom && fecha <= hotel.availabilityTo;
    }
  });

  return (
    <div className="App">
      <Header pais={pais} desde={desde} hasta={hasta} />
      <Filtros
        pais={pais}
        tamaño={tamaño}
        precio={precio}
        desde={desde}
        hasta={hasta}
        setDesde={setDesde}
        setHasta={setHasta}
        setPais={setPais}
        setTamaño={setTamaño}
        setPrecio={setPrecio}
      />
      {hotelesFiltrados.length === 0 ? (
        <p className="mensajeError">
          No hay hoteles disponibles bajo los parámetros escogidos.
        </p>
      ) : (
        <Hoteles listaHoteles={hotelesFiltrados} />
      )}
    </div>
  );
}
